import Panel from "@/components/Panel";
import DepositClient from "@/components/deposit/DepositClient";
import { getServerApiBase } from "@/lib/api";
import { redirect } from "next/navigation";
import { buildServerCookieHeader } from "@/lib/server/cookies";

async function fetchUsers() {
  const cookieHeader = await buildServerCookieHeader();
  const res = await fetch(`${getServerApiBase()}/users`, {
    headers: cookieHeader ? { Cookie: cookieHeader } : undefined,
    cache: "no-store",
  });
  if (res.status === 401) redirect("/login");
  if (!res.ok) throw new Error("Failed to load members");
  return res.json();
}

export default async function DepositPage() {
  const users = await fetchUsers();
  return (
    <div className="space-y-6">
      <Panel
        title="Capture contributions"
        description="Pick the member, type in the amount, and let the system suggest instalments when dues exist."
      >
        <DepositClient users={users} />
      </Panel>
    </div>
  );
}
